@mixin defaultAlert() {
    background : #eeeeee;
    padding : 15px;
    border-radius : 5px;
    max-width : 500px;
    width : 100%;
    margin : auto;
}

.my-alert {
    @include defaultAlert()
}
.my-alert_red {
    @extend .my-alert;
    background : #ff8a8a;
}

.my-alert_navy {
    @extend .my-alert;
    background: #8f8fff;
}

.my-alert_yellow {
    @extend .my-alert;
    background: #f8e8a8;
}